.home-container {
    width: 100%;
    padding-top: 40px;
}

.home-container .home-intro {
    width: 900px;
    padding: 10px 20px;
    margin: 0 auto;
    box-shadow: 8px 8px 2px #84693a;
    border-radius: 5px;
    background-color: #9e8863;
}

.home-container .intro-text {
    font-size: 17px;
    direction: rtl;
    font-family: IranSans;
    text-align: justify;
    line-height: 1.5;
}

.home-container .intro-video {
    width: 100%;
    height: 100%;
    border-radius: 10px;
}

.home-container .intro-video-container {
    width: 700px;
    height: 400px;
    margin: 0 auto;
    margin-top: 50px;
}

@media only screen and (max-width: 1000px) {
    .home-container .home-intro {
        width: 90%;
        padding: 10px 20px;
    }
}

@media only screen and (max-width: 800px) {
    .home-container {
        padding-top: 0;
    }

    .home-container .intro-text {
        font-size: 15px;
    }

    .home-container .intro-video-container {
        width: 90%;
    }
}

@media only screen and (max-width: 480px) {
    .home-container .intro-video-container {
        width: 98%;
    }
}