.donate-container {
    width: 100%;
    padding-top: 40px;
}

.donate-container .donate-intro {
    width: 900px;
    padding: 10px 20px;
    margin: 0 auto;
    box-shadow: 8px 8px 2px #84693a;
    border-radius: 5px;
    background-color: #9e8863;
}

.donate-container .intro-text {
    font-size: 17px;
    direction: rtl;
    font-family: IranSans;
    text-align: justify;
    line-height: 1.5;
}

.donate-container .donate-button {
    cursor: pointer;
    font-family: IranSans;
    font-size: 20px;
    direction: rtl;
    text-align: center;
    width: 100px;
    padding: 10px 20px;
    margin: 0 auto;
    margin-top: 50px;
    border-radius: 5px;
    background-color: #9e8863;
    animation-name: donate-button-anime;
    animation-duration: 1s;
    animation-timing-function: ease-in-out;
    animation-iteration-count: infinite;
    animation-direction: alternate;
}

@keyframes donate-button-anime {
    from { box-shadow: 0 0 1px 1px #84693a; }
    to { box-shadow: 0 0 5px 5px #84693a; }
}

.donate-line {
    width: 920px;
    height: 2px;
    margin: 0 auto;
    margin-top: 50px;
    background-color: #9e8863;
}

.top-donates {
    width: 100%;
    margin-top: 50px;
}

.top-donate {
    width: 900px;
    padding: 18px;
    margin: 0 auto;
    margin-bottom: 30px;
    background-color: #9e8863;
    border-radius: 5px;
}

.top-donate-info {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    justify-content: space-between;
    font-family: IranSans;
    font-size: 17px;
    padding: 10px;
    border-radius: 5px;
    background-color: #84693a;
}

.top-donate-price {
    direction: rtl;
}

.top-donate-comment {
    direction: rtl;
    text-align: justify;
    margin-top: 10px;
    font-family: IranSans;
    line-height: 1.5;
    font-size: 15px;
}

@media only screen and (max-width: 1000px) {
    .donate-container .donate-intro {
        width: 95%;
        padding: 10px 15px;
        box-shadow: 4px 4px 2px #84693a;
    }

    .donate-line {
        width: 97%;
    }

    .top-donate {
        width: 95%;
    }
}

@media only screen and (max-width: 550px) {
    .donate-container {
        padding-top: 10px;
    }

    .donate-container .intro-text {
        font-size: 15px;
    }

    .top-donate-info {
        font-size: 16px;
    }

    .top-donate-comment {
        font-size: 14px;
    }
}

@media only screen and (max-width: 400px) {
    .top-donates {
        margin-top: 30px;
    }

    .top-donate {
        padding: 10px;
    }
}