.key-name {
    font-weight: bolder;
    text-align: center;
    margin-bottom: 50px;
}

.key-name i {
    font-size: 25px;
    text-decoration: underline;
    color: #a3b18c;
}

.keys {
    width: 100%;
    padding: 10px;
    margin: 0 auto;
    display: flex;
    align-items: end;
    justify-content: center;
    border-radius: 10px;
    margin-bottom: 100px;
}

.keys table {
    font-family: 'Arial', sans-serif;
    display: inline-block;
    border-collapse: separate;
    border-spacing: 3px 6px;
    margin: 0 10px;
    user-select: none;
    cursor: default;
}

.keys td > p > span {
    text-decoration: none;
    color: white;
    font-size: 12px;
    background-color: transparent !important;
}

.keys td > p {
    text-decoration: none;
    color: white;
    font-size: 15px;
}

.keys td {
    background: #262626;
    padding: 10px;
    width: 60px;
    height: 60px;
    text-align: center;
    margin: 10px;
    border-radius: 10px;
}

@media only screen and (max-width: 1100px) {
    .keys {
        width: 100%;
        padding: 0;
        margin-bottom: 50px;
        transform: scale(0.8);
    }

    .key-name {
        margin-bottom: 20px;
    }
}

@media only screen and (max-width: 850px) {
    .keys {
        transform: scale(0.6);
    }

    .key-name {
        margin-bottom: 0;
    }
}

@media only screen and (max-width: 650px) {
    .keys {
        transform: scale(0.5);
        margin-bottom: 0;
        margin-top: -40px;
    }

    .key-name i {
        font-size: 20px;
    }
}

@media only screen and (max-width: 520px) {
    .keys {
        transform: scale(0.4);
        margin-top: -80px;
    }

    .key-name i {
        font-size: 18px;
    }
}

@media only screen and (max-width: 420px) {
    .keys {
        transform: scale(0.45);
        margin-top: -80px;
        margin-bottom: -50px !important;
    }

    .keys table:nth-child(2) {
        display: none;
    }
}