.speaker-container {
    margin-top: 90px;
}

.speaker-segment {
    margin-top: 100px !important;
}

.speaker-test {
    width: 100%;
    margin-top: 70px;
}

.speaker-test-name {
    font-size: 25px;
    color: #a3b18c;
    font-family: IranSans;
    font-weight: bold;
    margin: 0 auto;
    text-align: center;
}

.test-buttons {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    margin: 50px;
    font-family: IranSans;
    font-size: 25px;
}

.test-button {
    background: #a3b18c;
    padding: 10px 20px;
    margin: 0 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: 0.3s;
}

.test-button:hover {
    opacity: 0.6 !important;
}

@media only screen and (max-width: 1100px) {
    .speaker-test {
        margin-top: 50px;
    }

    .speaker-test-name {
        font-size: 24px;
    }

    .test-buttons {
        margin: 30px;
        font-size: 20px;
    }

    .test-button {
        margin: 0 8px;
    }
}

@media only screen and (max-width: 800px) {
    .speaker-test {
        margin-top: 50px;
    }

    .speaker-test-name {
        font-size: 22px;
    }

    .test-buttons {
        flex-wrap: wrap;
        font-size: 17px;
    }

    .speaker-test:nth-child(2) .test-buttons {
        margin-top: 20px;
    }

    .speaker-test:nth-child(2) .test-button {
        width: 100px;
        margin: 10px 20px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}

@media only screen and (max-width: 480px) {
    .speaker-container {
        margin-top: 60px;
    }

    .speaker-segment {
        margin-top: 50px !important;
    }
}