.Header {
  width: 100%;
  text-align: center;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  padding: 10px 80px;
  margin-bottom: 40px;
}

.Header .title {
  font-family: Coiny;
  font-size: 40px;
  font-weight: bold;
  color: #a3b18c;
}

.Header .menu {
  width: 500px;
  display: flex;
  margin-top: 25px;
  flex-direction: row-reverse;
  align-items: center;
  justify-content: space-between;
}

.Header .menu-item {
  color: #c3e0cb;
  font-size: 20px;
  position: relative;
  height: 50px;
  width: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-family: IranSans;
  text-decoration: none;
  transition: 0.2s;
  border-radius: 5px;
  background-color: #516b58;
}

.Header .menu-item:hover {
  opacity: 0.7;
}

.Header .menu-underline {
  width: 90%;
  position: absolute;
  bottom: -10px; /* Adjust this value to position the underline */
  left: 5%;
  right: 0;
  height: 2px; /* Thickness of the underline */
  background-color: #516b58; /* Color of the underline */
  transform: scaleX(0); /* Initially hide the underline */
  transition: transform 0.25s ease; /* Smooth transition */
}

.laptop-text-mobile {
  display: none;
}

.menu-item.active .menu-underline {
  transform: scaleX(1);
}

@media only screen and (max-width: 1000px) {
  .Header {
    padding: 10px 20px;
  }
}

@media only screen and (max-width: 900px) {
  .Header .title {
    font-size: 30px;
  }

  .Header .menu {
    margin-top: 10px;
    justify-content: space-evenly;
  }

  .Header .menu-item {
    height: 40px;
    width: 120px;
    font-size: 17px;
    border-radius: 3px;
  }
}

@media only screen and (max-width: 700px) {
  .Header {
    padding: 10px 5px;
  }

  .Header .title {
    font-size: 25px;
  }

  .Header .menu-item {
    height: 40px;
    width: 100px;
    font-size: 15px;
  }
}

@media only screen and (max-width: 550px) {
  .Header {
    flex-direction: column;
    padding: 10px 0 !important;
  }

  .Header .menu {
    width: 100%;
  }

  .laptop-text, .tester-text {
    display: none;
  }

  .laptop-text-mobile {
    display: block;
    margin-bottom: 20px;
  }
}

@media only screen and (max-width: 320px) {
  .Header .menu-item {
    width: 80px;
    font-size: 13px;
  }
}