.tests-container {
    width: 100%;
    margin-top: 100px;
}

.back {
    width: 50px;
    height: 40px;
    padding: 5px;
    cursor: pointer;
    margin-left: 400px;
    border-radius: 5px;
    background-color: #516b58;
}

.back:hover {
    opacity: 0.7;
}

.tests {
    width: 800px;
    margin: 0 auto;
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    align-items: center;
    justify-content: space-around;
}

.test {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    width: 150px;
    height: 150px;
    padding: 20px;
    margin: 20px;
    transition: 0.2s;
    cursor: pointer;
    border-radius: 8px;
    background-color: #516b58;
}

.test:hover {
    opacity: 0.7;
}

.test .image {
    width: 60px;
    height: 60px;
}

.test .name {
    color: #a3b18c;
    font-size: 18px;
    font-family: IranSans;
}

.test .mouseandkeyboard {
    width: 100px;
}

/* Test Description */

.test-container {
    width: 100%;
    margin-top: 120px;
}

.test-container .segment {
    width: 1000px;
    margin: 0 auto;
    margin-bottom: 35px;
    padding: 20px;
    border-radius: 5px;
    font-size: 17px;
    font-family: IranSans;
    line-height: 1.8;
    text-align: justify;
    direction: rtl;
    background-color: #9e8863;
}

.test-container span:not(.test-video-container *) {
    background-color: #8a7653;
    padding: 4px 6px;
    border-radius: 5px;
    cursor: pointer;
}

.test-container span:hover {
    opacity: 0.8;
}

.test-container img {
    display: block;
    width: 85%;
    margin: 0 auto;
    margin-top: 25px;
    border-radius: 8px;
    cursor: pointer;
}

.test-container img:hover {
    opacity: 0.8;
}

.test-video-container {
    width: 90%;
    display: block;
    margin: 0 auto;
    margin-top: 20px;
    direction: ltr;
}

.voice-visualizer__audio-info-container {
    display: none !important;
}

.voice-visualizer__buttons-container button img {
    margin: auto !important;
    border-radius: 0 !important;
}

.camera-container {
    position: relative;
    width: 700px;
    height: 350px;
    margin: 0 auto;
    margin-top: 50px;
    margin-bottom: 150px;
    border-radius: 8px;
    cursor: pointer;
    background-color: #a3b18c;
}

.camera-container p {
    position: absolute;
    color: white;
    text-align: center;
    direction: rtl;
    width: 100%;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 20px;
    font-weight: bold;
    z-index: 9999;
}

.camera-container select {
    position: absolute;
    left: 50%;
    bottom: -20%;
    transform: translate(-50%, 0);
    border-radius: 8px;
    outline: none;
    background-color: #a3b18c;
    padding: 10px 20px;
}

.camera {
    width: 100%;
    height: 100%;
    margin: 0 auto;
}

@media only screen and (max-width: 1100px) {
    .test-container .segment {
        width: 95%;
    }
}

@media only screen and (max-width: 1000px) {
    .tests {
        width: 95%;
    }
}


@media only screen and (max-width: 800px) {
    .tests {
        width: 87%;
    }

    .test {
        width: 120px;
        height: 120px;
        padding: 15px;
        margin: 10px;
    }

    .test .image {
        width: 45px;
        height: 45px;
    }

    .test .name {
        font-size: 15px;
    }

    .test .mouseandkeyboard {
        width: 85px;
    }

    .test-container .segment {
        width: 97%;
        font-size: 16px;
    }

    .camera-container {
        width: 95%;
    }
}

@media only screen and (max-width: 650px) {
    .test-container {
        margin-top: 50px;
    }

    .tests-container {
        margin-top: 50px;
    }

    .tests {
        width: 100%;
    }
}

@media only screen and (max-width: 550px) {
    .test-container .segment {
        font-size: 15px;
    }

    .test-container img {
        width: 100%;
    }

    .test-video-container {
        width: 100%;
    }

    .camera-container {
        height: 200px;
    }

    .camera-container p {
        font-size: 18px;
    }
}

@media only screen and (max-width: 420px) {
    .test {
        width: 90px;
        height: 90px;
        padding: 8px;
        margin: 8px;
    }

    .test .mouseandkeyboard {
        width: 65px !important;
    }

    .test .image {
        width: 35px;
        height: 35px;
    }

    .test .name {
        font-size: 12px;
    }
}