.screen-test-buttons {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 auto;
    margin-bottom: 100px;
}

.screen-test-buttons p {
    text-align: center;
    color: #c2c499;
    background-color: #84693a;
    font-size:  25px;
    border-radius: 5px;
    margin: 20px auto;
    font-weight: bold;
    padding: 10px 20px;
    cursor: pointer;
    transition: 0.2s;
}

.screen-test-buttons p:hover {
    opacity: 0.7;
}

.test-color-container {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 9999;
    cursor: none;
}

@media only screen and (max-width: 900px) {
    .screen-test-buttons {
        margin-bottom: 50px;
    }

    .screen-test-buttons p {
        font-size:  20px;
    }
}

